






























a, button {
      @apply text-xs text-gray-500 leading-none;

      &:hover {
        @apply text-gray-400;
      }

      &.dropdown-trigger {
        &:after {
          content: " \f107";
          font-family: "Font Awesome 5 Pro";
          font-weight: 400;
          font-size: theme('fontSize.sm');
          vertical-align: -1px;
        }

        &:hover {
          &:after {
            @apply text-gray-400;
          }
        }
      }
    }
