#homepage-search{top:0;width:100%;height:100%;@apply flex flex-col items-center justify-center absolute z-10;background:rgba(0,0,0,.7)}input,select{@apply block px-3 py-2 w-full mt-2 leading-loose rounded-none}input::placeholder,select::placeholder{@apply text-gray-300}select{-moz-appearance:none;-webkit-appearance:none;@apply bg-white relative}.select-wrapper{@apply relative}.select-wrapper:after{content:" ";font-family:"Font Awesome 5 Pro";font-weight:400;font-size:theme("fontSize.sm");vertical-align:-1px;color:#000;position:absolute;right:10px;top:35%}.form-row{@apply font-light text-sm w-full}