body{
  background-color:#F5F6F6;
  font-family: Lato, Avenir, -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Arial, "Noto Sans", sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol", "Noto Color Emoji";
  font-weight: 400;
  line-height: 1;
  letter-spacing:2px
}
*:focus,button:focus{
  outline: 0
}
a,button{
  -webkit-transition-property:color,background,border;
  transition-property:color,background,border;
  -webkit-transition-duration:.2s;
  transition-duration:.2s;
  -webkit-transition-timing-function:ease-in-out;
  transition-timing-function:ease-in-out
}
a:after,a:before,button:after,button:before{
  -webkit-transition-property:all;
  transition-property:all;
  -webkit-transition-duration:1s;
  transition-duration:1s;
  -webkit-transition-timing-function:ease-in-out;
  transition-timing-function:ease-in-out
}
.container{
  width: 100%;
}
@media (min-width: 640px){
.container{
    max-width: 640px;
}
}
@media (min-width: 768px){
.container{
    max-width: 768px;
}
}
@media (min-width: 1024px){
.container{
    max-width: 1024px;
}
}
@media (min-width: 1280px){
.container{
    max-width: 1280px;
}
}
