@keyframes fadeDropIn{from{opacity:0;transform:translateY(-1em) perspective(600px)}to{opacity:1;transform:translateY(0) perspective(0)}}@keyframes fadeIn{from{opacity:0}to{opacity:1}}.dropdown{@apply absolute border border-gray-200 bg-white text-sm z-50;top:180%;animation:fadeDropIn .25s;animation-fill-mode:both}.dropdown.arrow:before{z-index:100;border-style:solid;border-width:6px;border-color:#fff transparent transparent #fff;width:0;height:0;background:transparent;box-shadow:-1px -1px 0 0 rgba(0,0,0,.15);transform:rotate(45deg);animation:fadeIn .25s;animation-fill-mode:both;position:absolute;top:-5px;opacity:1;left:auto;right:.8rem;content:"";display:block}.dropdown:not(.arrow){top:calc(100% - 1px)}.dropdown.anchor-right{@apply right-0}.dropdown.anchor-left{@apply left-0}.dropdown li{@apply m-0 !important}.dropdown li button,.dropdown li a{@apply block pl-3 pr-12 py-3 w-full mt-1 text-left border-l-4 border-transparent;@apply text-gray-400}.dropdown li button:hover,.dropdown li button.active,.dropdown li a:hover,.dropdown li a.active{@apply relative;@apply border-blue-500 bg-blue-100 text-gray-600}.dropdown li:first-child button{@apply mt-0}.dropdown.checkArrow li button.active:after,.dropdown.checkArrow li a.active:after{position:absolute;right:1rem;top:auto;content:"";font-family:"Font Awesome 5 Pro";font-weight:400;color:theme("colors.blue.500")}.dropdown.orange li button,.dropdown.orange li a{@apply text-gray-400}.dropdown.orange li button:hover,.dropdown.orange li button.active,.dropdown.orange li a:hover,.dropdown.orange li a.active{@apply border-orange-500 bg-orange-100 text-gray-600}.dropdown.orange.checkArrow li button.active:after,.dropdown.orange.checkArrow li a.active:after{color:theme("colors.orange.500")}