






button {
  @apply cursor-pointer border-0 leading-none;
  letter-spacing: inherit;

  &:not([class*="p-"]):not([class*="pl-"]:not([class*="pr-"])):not([class*="px-"]):not([class*="py-"]) {
    @apply p-0
  }

  &:not([class*="m-"]):not([class*="ml-"]:not([class*="mr-"])):not([class*="mx-"]):not([class*="my-"]) {
    @apply m-0
  }

  &:disabled {
    filter: opacity(40%);
    pointer-events:none;
  }

  &.big-padded { // @TODO: This does not seem to work on iPhone. Investigate
    @apply w-full relative p-3 inline-block text-white text-left;

    &:after {
      position: absolute;
      right: 1rem;
      top: auto;
      content: "\f105";
      font-family: "Font Awesome 5 Pro";
      font-weight: 400;
      color: theme('colors.white') // Default color
    }
  }

  &.gray-dark {
    @apply bg-gray-700 text-white;

    &:hover {
      @apply bg-gray-600;
    }
  }

  &.gray-med {
    @apply bg-gray-500 text-white;

    &:hover {
      @apply bg-gray-700;
    }
  }

  &.orange {
    @apply bg-orange-600 text-white;

    &:hover {
      @apply bg-orange-700;
    }
  }

}
