button{@apply cursor-pointer border-0 leading-none;letter-spacing:inherit}button:not([class*=p-]):not([class*=pl-]:not([class*=pr-])):not([class*=px-]):not([class*=py-]){@apply p-0}button:not([class*=m-]):not([class*=ml-]:not([class*=mr-])):not([class*=mx-]):not([class*=my-]){@apply m-0}button:disabled{filter:opacity(40%);pointer-events:none}button.big-padded{@apply w-full relative p-3 inline-block text-white text-left}button.big-padded:after{position:absolute;right:1rem;top:auto;content:"";font-family:"Font Awesome 5 Pro";font-weight:400;color:theme("colors.white")}button.gray-dark{@apply bg-gray-700 text-white}button.gray-dark:hover{@apply bg-gray-600}button.gray-med{@apply bg-gray-500 text-white}button.gray-med:hover{@apply bg-gray-700}button.orange{@apply bg-orange-600 text-white}button.orange:hover{@apply bg-orange-700}