








































@tailwind base;

body {
  background-color: theme('colors.gray.100');
  @apply font-sans font-normal leading-none;
  letter-spacing: 2px;
}

*:focus, button:focus {
  @apply outline-none;
}

a, button {
  transition-property: color, background, border;
  transition-duration: 0.2s;
  transition-timing-function: ease-in-out;

  &:after, &:before {
    transition-property: all;
    transition-duration: 1s;
    transition-timing-function: ease-in-out;
  }
}

@tailwind components;

.max-w-wrapper {
  @apply mx-auto px-4;
  max-width: calc(1050px + 2rem);
}

ul.inline {
  @apply flex items-center items-start;
}

@tailwind utilities;

// Flex utilities
.box-shadow {
  box-shadow: 0 0 0 2px #ee3820, 0 0 20px rgba(238,56,32,.5);
}

.text-xxs {
  font-size: 0.65rem;
}

/**
 * Here you would add any custom utilities you need that don't come out of the
 * box with Tailwind.
 *
 * Example :
 *
 * .bg-pattern-graph-paper { ... }
 * .skew-45 { ... }
 *
 * Or if using a preprocessor or `postcss-import`:
 *
 * @import "utilities/background-patterns";
 * @import "utilities/skew-transforms";
 */

@import url('https://fonts.googleapis.com/css?family=Lato:300,400,700|Cormorant+Garamond:400,700&display=swap&subset=latin-ext');

#app {
  font-family: 'Lato', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: theme('colors.gray.900');
  font-size: theme('fontSize.base');
  font-weight: 300;
  @apply leading-none;
}

#nav {
  padding: 30px;
  a {
    font-weight: bold;
    color: #2c3e50;
    &.router-link-exact-active {
      color: #42b983;
    }
  }
}
